* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main {
  height: 100vh;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background: linear-gradient(45deg,#c58be5, #a8c0ee 50%, #ffb7fd);
}

.center {
  min-height: 40vh;
  height: 80vh;
  width: 25%;
  position: relative;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: linear-gradient(90deg,#ead6ee,#a0f1ea );
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
}

h1 {
  position: relative;
  font-weight: bold;
  text-align: center;
  text-transform: capitalize;
  color: white;
  text-shadow: 3px 4px 4px black;
}
input{
  width: 50%;
  font-size: 15px;
  font-weight: 400;
  outline: none;
  border: none;
  border-bottom: 5px solid violet;
  background: transparent;
  text-align: center;
}
.btn{
  height: 40px;
  width: 40px;
  font-size: 20px;
  margin-left: 20px !important;
  border: transparent;
  border-radius: 50% !important;
  outline: none;
  color: black !important;
  background-color: violet !important;
}
.btn:hover{
  background-color: green !important;
  color: white !important;
}
ol{
  margin-top: 20px;
}
ol li{
  text-align: start;
  align-items: center;
  font-size: 22px;
  font-weight: bold;
  color: #000;
  list-style: none;
  margin: 10px 0px;
}
.todolist{
  width: 100%;
  text-align: start;
  display: flex;
  align-items: center;
  gap: 15px;
  margin-left: 20px;
}
.icon{
  min-height: 30px !important;
  width: 30px !important;
  padding: 5px 5px;
  border-radius: 50%;
  background-color: violet !important;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  cursor: pointer !important;
  
}
.icon:hover{
  background-color: red !important;
}

@media (max-width:912px) {
  .center{
    width: 80% !important;
  }
 
}